import React from 'react';
import "./home.css";
import Photo from '../../assets/images/perfil.png'

const Home = () => {
    return (
        <section>
            <div className='section-home'>
                <div className='home-description'>
                    <h2 className='header-name'>Hola, soy Tito</h2>
                    <h1 className='header-title'>Full-Stack</h1>
                    <h2 className='header-subtitle'>Developer</h2>
                    <p className='header-description'>Vivo en constante aprendizaje para ir mejorando mis develops skills</p>
                </div>
                <div className='home-photo'>
                    <img className='my-photo' alt='mi persona' src={Photo}/>
                    <div className='blob'></div>
                </div>
            </div>
        </section>
    );
}

export default Home;
