import React from 'react';
import "./footer.css";
import LogoFooter from "../../assets/images/logofooter.svg";
import { SlSocialGithub, SlSocialLinkedin, SlSocialInstagram } from 'react-icons/sl';

const Footer = () => {
    return (
        <footer>
            <div className='footer-container'>
                <img className='footer-logo' alt='logo' src={LogoFooter}/>
                <ul className='footer-social-icons'>
                    <li>
                        <a target='_blank' rel='noreferrer' title='Instagram' href='https://www.instagram.com/'><SlSocialLinkedin/></a>
                    </li>
                    <li>
                        <a target='_blank' rel='noreferrer' title='Instagram' href='https://www.instagram.com/'><SlSocialGithub/></a>
                    </li>
                    <li>
                        <a target='_blank' rel='noreferrer' title='Instagram' href='https://www.instagram.com/'><SlSocialInstagram/></a>
                    </li>
                </ul>
            </div>
        </footer>
    );
}

export default Footer;
