import React from 'react';
import "./projects.css";
import ProjectCard from '../../components/card/project/ProjectCard.jsx';
import Data from './Data';
import { motion } from 'framer-motion';

const faceInAnimationsVariants = {
    initial: {
        opacity: 0,
        y: 100,
    },
    animate: (id) => ({
        opacity: 1,
        y: 0,
        transition: {
            delay: 0.1 * id,
            ease: "linear"
        }
    }),
};

const Projects = () => {
    return (
        <section>
            <div className='section-projects'>
                <div className='section-header'>
                    <h2 className='header-title'>Projectos</h2>
                    <p className='header-paragraph'>Explora mis creaciones</p>
                </div>
                <div className='projects-container'>
                    {Data.map((val, id) => {
                        return (
                            <motion.div
                                key={id}
                                variants={faceInAnimationsVariants}
                                initial="initial"
                                whileInView="animate"
                                viewport={{
                                    once: false,
                                }}
                                custom={id}
                                >
                                <ProjectCard key={id} title={val.title} desc={val.desc} lang={val.lang}/>
                            </motion.div>
                        )
                    })}
                </div>
            </div>
        </section>
    );
}

export default Projects;
