import React, { useState, useEffect } from 'react';
import "./navbar.css";
import { SlSocialGithub, SlSocialLinkedin, SlSocialInstagram, SlHome, SlUser, SlEnvolope, SlGraduation } from 'react-icons/sl'; // Corregí el nombre del ícono "SlEnvolope" a "SlEnvelope"
import Logo from '../../assets/images/logo.svg';

const Navbar = () => {
    const [isMenuOpen, setMenuOpen] = useState(false);
    const [activeSection, setActiveSection] = useState('home');

    const toggleMenu = () => {
        setMenuOpen(!isMenuOpen);
    };

    const styleToggle = isMenuOpen ? "menu-toggle open" : "menu-toggle";
    const styleNavItems = isMenuOpen ? "nav-items open" : "nav-items";

    const scrollToSection = (sectionId) => {
        const section = document.getElementById(sectionId);
        if (section) {
            section.scrollIntoView({ behavior: "smooth" });
            setActiveSection(sectionId);
        }
        setMenuOpen(false);
    };
    

    useEffect(() => {
        // Función para manejar el cambio de tamaño de la ventana
        const handleResize = () => {
            if (window.innerWidth > 768) {
                setMenuOpen(false); // Cierra el menú si la pantalla es más grande que 768px
            }
        };

        // Agregar el evento de cambio de tamaño de la ventana
        window.addEventListener('resize', handleResize);

        // Limpieza del evento al desmontar el componente
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    return (
        <nav>
            <div className='nav-container'>
                <a className='nav-logo' href='#home'>
                    <img src={Logo} alt='logo' />
                </a>
                <ul className={styleNavItems}>
                    <li className={activeSection === 'home' ? 'active' : ''} onClick={() => scrollToSection('home')}>
                        <SlHome className='nav-items-icon' size={24} />
                        <a
                            id='home'
                            href='#home'
                        >
                            Home
                        </a>
                    </li>
                    <li className={activeSection === 'about-me' ? 'active' : ''} onClick={() => scrollToSection('about-me')}>
                        <SlUser className='nav-items-icon' size={24} />
                        <a
                            id='about-me'
                            href='#about-me'
                        >
                            Sobre mí
                        </a>
                    </li>
                    <li className={activeSection === 'projects' ? 'active' : ''} onClick={() => scrollToSection('projects')}>
                        <SlGraduation className='nav-items-icon' size={24} />
                        <a
                            id='projects'
                            href='#projects'
                        >
                            Proyectos
                        </a>
                    </li>
                    <li className={activeSection === 'contact' ? 'active' : ''} onClick={() => scrollToSection('contact')}>
                        <SlEnvolope className='nav-items-icon' size={24} />
                        <a
                            id='contact'
                            href='#contact'
                        >
                            Contacto
                        </a>
                    </li>
                </ul>
                <ul className='nav-social-icons'>
                    <li>
                        <a target='_blank' rel='noreferrer' title='LinkedIn' href='https://www.linkedin.com/'><SlSocialLinkedin /></a>
                    </li>
                    <li>
                        <a target='_blank' rel='noreferrer' title='GitHub' href='https://www.github.com/'><SlSocialGithub /></a>
                    </li>
                    <li>
                        <a target='_blank' rel='noreferrer' title='Instagram' href='https://www.instagram.com/'><SlSocialInstagram /></a>
                    </li>
                </ul>
                <div className={styleToggle} onClick={toggleMenu}>
                    <span />
                </div>
            </div>
        </nav>
    );
}

export default Navbar;
